@import url(https://db.onlinewebfonts.com/c/01661f50354ecd0a3560ac450ecf43d3?family=Bell+MT+Regular);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

/* //navbar css */
.navbar {
  font-family: "Bell MT Regular";
  font-size: 13px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
  /* border-radius: 10% 10% 0 0 / 7px; */
  position: sticky; /* add this line */
  top: 0; /* add this line */
  z-index: 1000; /* add this line */
  width: 100%; /* add this line */
}

.nav-link {
  font-weight: 300 !important;
}
.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
}


.search-form {
  height: 25px;
  margin-right: 5px;
  margin-top: 5px;
}
.search-form input {
  border: none;
  border-radius: 10;
}
.search-form button {
  background-color: white;
  border-radius: 0;
  color: black;
  border-radius: 10px;
}
/* =============================
==========footer============= */
footer {
  font-family: "Bell MT Regular";
  background-color: #910000;
  color: white;
  padding: 10px;
  display: flex;
  justify-content: center;
  border-radius: 25px / 0 0 25% 25%;
  text-align: center;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
}

.footer-section {
  flex-basis: calc(33.333% - 20px);
  margin-bottom: -30px;
}

.footer-section h2 {
  font-size: 1.8rem;
  margin-bottom: 5px;
}

.footer-section p {
  margin-bottom: 10px;
}

.footer-social ul {
  list-style: none;
  padding: 15px;
  margin: 15px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.footer-social li {
  margin-right: 20px;
  margin-bottom: 0px;
}

.footer-social a {
  display: block;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-size: contain;
}

.footer-company p {
  line-height: 1.5;
}

.footer-contact a {
  color: white;
  text-decoration: none;
}

@media (max-width: 768px) {
  .footer-section {
    flex-basis: 100%;
  }
}
.footer-social img{
  margin-left: 60px;
  align-items: center;
}
.footer-social p{
  margin-left: 31px;
  text-align: center;
}
/* =================================
======== page not found css ======= */
.pnf {
  font-family: "Bell MT Regular";
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #910000;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: #910000;
  border: 1px solid #910000;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: #910000;
  color: white;
}
/* ========================================= */
/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ==================== */
.product-link {
  font-family: "Bell MT Regular";
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  font-family: "Bell MT Regular";
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  font-family: "Bell MT Regular";
  background-color: #910000;
  color: white;
}
/* ================
====dashboard
=================== */

.dashboard {
  font-family: "Bell MT Regular";
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  font-family: "Bell MT Regular";
  background-color: #910000;
  color: white;
  padding: 20px 0px;
}
/* carousel */
.carousel{
  margin-top: 5px;
}
.box{
  background-color: BLACK;
  color: white;
  height: 30px;
  width: 100%;
  text-align: center;
  margin-top: 1px;
}